import React from "react";
import { useNavigate } from "react-router-dom";
import "../styles/Letter.css";

const Letter = () => {
  const navigate = useNavigate();

  const goToHomePage = () => {
    navigate("/");
  };

  return (
    <div className="letter-container">
      <div className="letter-content">
        <h1>Para mi atpesita </h1>
        <p>Hola bonita,</p>
        <p>
          Espero que te est gustando este pequeo viajecito por las estrellas,
          y que te est haciendo ilusin todo lo que has visto hasta ahora.
          Esta fue la forma que encontr para decirte lo mucho que te amo y de alguna
          forma dejar un pedacito de mi y lo que me gusta en esta propuesta y "regalo".
        </p>
        <p>
          Quiero que quede ms que claro lo mucho que te amo y lo feliz que me has
          hecho hasta hoy. No sabes la emocin que tengo de poder seguir agregando
          das, fotos, canciones, lugares y momentos contigo a este gran archivo que
          ser nustra relacin.
        </p>
        <p>
          Podra extenderme por horas y escribir 5 prrafos ms (igual lo har
          en futuros regalos y cositas, no te libras). Pero supongo que para este
          punto en el recorrido por Orin ya me tendrs al lado tuyo muerto de las
          ganas de que visites "Alnilam" para una preguntita que te tengo preparada.
          As que no me extiendo ms. Eres y seguirs siendo la cosita mas increble
          que me ha pasado y una de las decisiones y logros que ms me llenan de orgullo.
        </p>
        <p>
          Te adora, <br />
          tu electroncito.
        </p>
        <button className="home-button" onClick={goToHomePage}>
        <i className="fas fa-home"></i> Home
      </button>
      </div>
    </div>
  );
};

export default Letter;